import axios from 'axios';
import { EndpointConfig } from '../config/config';

export default class OrderRepository {
  // Definir el cliente Axios para consumir el servicio
  get ws() {
    const cfg = new EndpointConfig();
    const ws = axios.create({
      baseURL: cfg.URL_DEV,
      headers: cfg.headerToken(),
    });
    return ws;
  }

  async getAllOrders() {
    const endpoint = '/deliveries';
    const res = await this.ws.get(endpoint);
    return res.data;
  }

  async getOrderById(id) {
    const endpoint = `/deliveries/${id}`;
    const res = await this.ws.get(endpoint);
    return res.data;
  }

  //   async update(id, body) {
  //     const endpoint = `/users/${id}`;

  //     let res = await this.ws.put(endpoint, body);
  //     return res;
  //   }

  async createOrder(body) {
    const endpoint = '/deliveries';

    const res = await this.ws.post(endpoint, body);
    return res.data;
  }

  //   async remove(id) {
  //     const endpoint = `/users/${id}`;

  //     let res = await this.ws.delete(endpoint);
  //     return res;
  //   }
}
